import { Compute } from './compute.js';
export class Calculate {
    constructor(data) {
        this.data = data;
        this.result;
        this.express;
        // this.priority = {};
        // this.setPriority();
    }
    setPriority() {
        this.priority.set('', -1);
        this.priority.set('+', 1);
        this.prioirty.set('-', 1);
        this.priority.set('*', 2);
        this.priority.set('/', 2);
    }

    /**
     * opt
     * @param {*} opt 
     */
    getsPriority(opt) {
        if (opt === '+' || opt === '-') {
            return 1;
        } else if (opt === '*' || opt === '/') {
            return 2;
        } else {
            return 0;
        }
    }

    /**
     * >,true
     * @param {*} opt 
     * @param {*} optStack 
     */
    isPriority(opt, optStack) {
        if (optStack.length === 0) {
            return true;
        } else {
            let opt2 = optStack[optStack.length - 1];
            if (this.getsPriority(opt) - this.getsPriority(opt2) >= 0) {
                return true;
            } else {
                return false;
            }
        }
    }

    /**
     * 
     * 
     */
    getResult() {
        if (true) {
            this.toPostExpression();
            this.calcExpression();
            console.log(this.result);
            return this.result;
        } else {
            return 'Input error';
        }

    }

    /** 
     * (^[\+\*\/])|([\+\-\*\/]$) 
     * ([\+\-\*\/][\+\*\/]+) [+,*,-,/][+,*,/]
     * ([\*\/](\-)+) [*,/][-]
     * ([\+\-](\-){2,}) [+,-][-]
     * @type RegExp
     */
    check() {
        //reg = /^(?!.*[^\d+\-*/\(\)])(?!.*\)\d)(?!.*[+\-*/]([+\-*/]|\)))(?!.*\([+*/])(?!.*(\d|\))\()(?=\d|\-|\()(?=.*(\d|\))$)[^\(\)]*(((?'open'\()[^\(\)]*)+((?'-open'\))[^\(\)]*)+)*(?(open)(?!))$/;
        let reg = /(^[\+\*\/])|([\+\-\*\/]$)|([\+\-\*\/][\+\*\/]+)|([\*\/](\-)+)|([\+\-](\-){2,})/;
        return reg.test(this.data);
    }

    /**
     * 
     * 
     */
    calcExpression() {
        let numStack = [],
            express = this.express;
        express.forEach(item => {
            if (!isNaN(item)) {
                numStack.push(item);
            } else {
                let num1 = numStack.pop(),
                    num2 = numStack.pop();
                console.log(numStack);
                let res = this.computeResult(num2, num1, item);
                numStack.push(res); //5 3 -,
            }
        });

        this.result = numStack.pop();
    }

    /**
     * 
     * @param {*} num1 
     * @param {*} num2 
     * @param {*} opt 
     */
    computeResult(num1, num2, opt) {
        let compute = new Compute(num1, num2);
        switch (opt) {
            case '+':
                return compute.add();
                break;
            case '-':
                return compute.sub();
                break;
            case '*':
                return compute.mul();
                break;
            case '/':
                return compute.div();
                break;
            default:
                return 'Error';
                break;
        }
    }

    /**
     * 
     * @param {*} item 
     */
    isNum(item) {
        let reg = /\d/;
        return reg.test(item);
    }

    /**
     * 
     * 
     * ) 
     * (  
     * 
     * 
     */
    toPostExpression() {
        let express = [],
            optStack = [];
        this.data = this.data.split('');
        let num = ''; //
        this.data.forEach((i) => {
            if (this.isNum(i)) { //
                num += i;
                //express.push(parseInt(i));
            } else {
                if (num !== '') {
                    express.push(parseInt(num));
                    num = '';
                }
                if (i === ')') { // 
                    let item = optStack.pop();
                    while (item != '(') {
                        express.push(item);
                        item = optStack.pop();
                    }
                } else if (i == '(' || this.isPriority(i, optStack)) {
                    optStack.push(i);
                } else {
                    express.push(optStack.pop());
                    optStack.push(i);
                }
            }
        });
        if (num !== '') {
            express.push(parseInt(num));
            num = '';
        }
        while (optStack.length > 0) {
            express.push(optStack.pop())
        }
        this.express = express;
        console.log(express);
    }
}


